import React, { useEffect, useState } from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
export default function Owel() {
    let [product, setproduct] = useState([])
    useEffect(() => {
        GetAllproduct()
    }, [])
    async function GetAllproduct() {
        let { data } = await axios.get(`https://fakestoreapi.com/products/`)
        // console.log(data);
        setproduct(data)
    }
    const options = {
        margin: 10,
        responsiveClass: true,
        nav: false,
        dots: true,
        loop: true,
        autoplayTimeout: 3000,
        autoplay: true,
        smartSpeed: 2000,
        responsive: {
            0: {
                items: 2,
            },
            400: {
                items: 2,
            },
            600: {
                items: 2,
            },
            700: {
                items: 3,
            },
            1000: {
                items: 4,

            }
        },
    };

    return (
        <div className='container'>
            <div className="row">
                    <OwlCarousel className='owl-theme py-5'{...options}>
                        {product.map((el) => {
                            return <div className="col" key={el.id}>
                            <div className="cardgruop ">
                                <div className="card position-relative shadow ">
                                    <img src={el.image} className="image" alt="" />
                                    <div className='laye d-flex justify-content-center align-items-center'>
                                            <Link className='text-decoration-none text-white' to={"/productdeteils/"+ el.id}>
                                        <div className='layr rounded-1  p-2 me-2 '>
                                                <i className="fa-solid fa-magnifying-glass fs-5"></i>
                                        </div>
                                            </Link>
                                        <div className='layr rounded-1  p-2'>
                                            <Link className="fa-solid fa-cart-shopping fs-5 text-white text-decoration-none" to="/cart"></Link>
                                        </div>
                                        <div className='layr rounded-1  p-2 ms-2'>
                                            <i className="fa-regular fa-heart fs-5"></i>
                                        </div>
    
    
                                    </div>
                                </div>
                                <div className='cloection text-center py-2'>
                                    <Link className='text-decoration-none text-dark' to={"/productdeteils/"+ el.id}>
                                        <h5 className='tex'>{el.title.split(" ").slice(0, 3).join(" ")}</h5>
                                    </Link>
                                </div>
    
                            </div>
    
    
                        </div>
                        })}
                    </OwlCarousel>
                </div>
            </div>

    )
}
