import React from 'react'
import { createHashRouter, RouterProvider } from 'react-router-dom'
import Cart from './Cart'
import { CartcontextProvider } from './Cartcontext'
import Category from './Category'
import Contact from './Contact'
import Home from './Home'
import Layout from './Layout'
import Login from './Login'
import Notfound from './Notfound'
import Plog from './Plog'
import Product from './Product'
import Productdeteils from './Productdeteils'
import Regstrition from './Regstrition'
export default function App() {
  let router = createHashRouter([
    {
      path: "", element: <Layout />, children: [
        { index: true, element: <Home /> },
        { path: "product", element: <Product /> },
        { path: "*", element: <Notfound /> },
        { path: "productdeteils/:id", element: <Productdeteils /> },
        { path: "category/:categ", element: <Category /> },
        { path: "contact", element: <Contact /> },
        { path: "cart", element: <Cart /> },
        { path: "plog", element: <Plog/> },
        { path: "regstrition", element: <Regstrition/> },
        { path: "login", element: <Login/> },
      ]

    }
  ])
  return (
    <div>
      <CartcontextProvider>
        <RouterProvider router={router} />
      </CartcontextProvider>
    </div>
  )
}
