import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import Owel from './Owel'
import $ from 'jquery';
import { Cartcontext } from './Cartcontext';



export default function Productdeteils(data) {
    let { id } = useParams()
    let [ditels, setditels] = useState([])
    let [rat, setrate] = useState([])
    const {addtocard} = useContext(Cartcontext)

    useEffect(() => {
        getProductdetiles(id)
    }, [id])

    async function getProductdetiles(id) {
        let { data } = await axios.get(`https://fakestoreapi.com/products/${id}`)
        setditels(data)
        setrate(data.rating)
        $(".loading").fadeOut(2000)
    }



    return (<>

        <div className='loading'>
            <div className="sk-circle">
                <div className="sk-circle1 sk-child"></div>
                <div className="sk-circle2 sk-child"></div>
                <div className="sk-circle3 sk-child"></div>
                <div className="sk-circle4 sk-child"></div>
                <div className="sk-circle5 sk-child"></div>
                <div className="sk-circle6 sk-child"></div>
                <div className="sk-circle7 sk-child"></div>
                <div className="sk-circle8 sk-child"></div>
                <div className="sk-circle9 sk-child"></div>
                <div className="sk-circle10 sk-child"></div>
                <div className="sk-circle11 sk-child"></div>
                <div className="sk-circle12 sk-child"></div>
            </div>
        </div>

        <div className='container mt-5 pt-5'>
            <div className="row justify-content-center g-5">
                <div className="col-md-3">
                    <div className="pic">
                        <img src={ditels.image} className='w-100 iimg' alt="" />
                    </div>
                </div>
                <div className="col-md-9 d-flex align-items-center">
                    <div className="dettext">
                        <h6 className='title py-2'><span className='text-primary fs-5 '>Title:</span> {ditels.title}</h6>
                        <h6 className='Dec'><span className='text-primary fs-5'>Description:</span> {ditels.description}</h6>
                        <h6 className='category py-2'><span className='text-primary fs-5 '>category:</span> {ditels.category}</h6>
                        <div className="rating d-flex justify-content-between">
                            <h6 className='price py-2'><span className='text-primary fs-5 '>price: </span> {ditels.price} Gp</h6>
                            <div className='rat'><i className="fa-solid fa-star text-warning fs-5"></i> {rat.rate}</div>
                        </div>
                        <button className='btn btn-outline-primary  me-2' onClick={()=> {addtocard(ditels , id)}}>+ Add to cart</button>
                        
                                <Link className="btn btn-primary btt py-1 fs-5 text-decoration-none" to="/cart">
                                <i className="fa-solid fa-cart-shopping"></i> go to cart
                                    
                                    </Link> 
                            
                    </div>
                </div>
            </div>
        </div >


        <Owel />



    </>

    )
}
