import React, { useContext, useEffect, useState } from 'react'
import { Link, NavLink } from 'react-router-dom'
import { Cartcontext } from './Cartcontext'
import { SearchContext } from './FilterPorducts-context'


export default function Navbar() {
    const { itemamount } = useContext(Cartcontext)
    const { getSearchValueFun } = useContext(SearchContext)
    let [search, setsearch] = useState("");


    useEffect(() => {
        getSearchValueFun(search)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [search])


    return (
        <nav className="navbar navbar-expand-lg mb-4 pt-3">
            <div className="container">
                <i className="fa-solid fa-shop fs-4 text-primary px-2"></i>
                <Link className="navbar-brand fs-5" to=""><span className='text-primary'>Bala</span> Store</Link>
                <span className="navbar-toggler border-0" type="submit" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className=''>
                        <i className="fa-solid fa-bars-staggered fs-1 text-primary"></i>
                    </span>
                </span>
                <div className="collapse navbar-collapse mt-2" id="navbarSupportedContent">
                    <ul className="navbar-nav m-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <NavLink className={({ isActive }) => isActive == true ? "nav-link test" : "nav-link"} aria-current="page" to="/">Home</NavLink>
                        </li>

                        <li className="nav-item dropdown">
                            <NavLink className={({ isActive }) => isActive == true ? "nav-link dropdown-toggle test" : "nav-link dropdown-toggle"} role="button" data-bs-toggle="dropdown" aria-expanded="false" aria-current="page" to="/shop">Shop</NavLink>
                            <ul className="dropdown-menu text-center py-2">
                                <p className='test' >Category</p>
                                <li><hr className="dropdown-divider" /></li>
                                <li><NavLink className={({ isActive }) => isActive == true ? "nav-link dropdown-item teste" : " dropdown-item"} to={"/category/electronics"}>Electronics</NavLink></li>
                                <li><NavLink className={({ isActive }) => isActive == true ? "nav-link dropdown-item teste" : " dropdown-item"} to={"/category/jewelery"}>jewelery</NavLink></li>
                                <li><NavLink className={({ isActive }) => isActive == true ? "nav-link dropdown-item teste" : " dropdown-item"} to={"/category/men's%20clothing"}>Men's clothing</NavLink></li>
                                <li><NavLink className={({ isActive }) => isActive == true ? "nav-link dropdown-item teste" : " dropdown-item"} to={"/category/women's%20clothing"}>Women's clothing</NavLink></li>
                            </ul>
                        </li>
                        <li className="nav-item">
                            <NavLink className={({ isActive }) => isActive == true ? "nav-link test" : "nav-link"} aria-current="page" to="/plog">Plog</NavLink>
                        </li>

                        <li className="nav-item dropdown">
                            <NavLink className={({ isActive }) => isActive == true ? "nav-link dropdown-toggle test" : "nav-link dropdown-toggle"} role="button" data-bs-toggle="dropdown" aria-expanded="false" aria-current="page" to="/shop">Pages</NavLink>
                            <ul className="dropdown-menu text-center py-2">

                                <li><hr className="dropdown-divider" /></li>
                                <li><NavLink className={({ isActive }) => isActive == true ? "nav-link dropdown-item teste" : " dropdown-item"} to={"/Cart"}>Cart</NavLink></li>
                                <li><NavLink className={({ isActive }) => isActive == true ? "nav-link dropdown-item teste" : " dropdown-item"} to={"/regstrition"}>Regstrition</NavLink></li>
                                <li><NavLink className={({ isActive }) => isActive == true ? "nav-link dropdown-item teste" : " dropdown-item"} to={"/login"}>Login</NavLink></li>
                            </ul>
                        </li>
                        <li className="nav-item">
                            <NavLink className={({ isActive }) => isActive == true ? "nav-link test" : "nav-link"} aria-current="page" to="/contact">Contact</NavLink>
                        </li>
                    </ul>

                    <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                        <div className='d-flex'>
                            <div className="collapse collapse-horizontal" id="collapseExample">
                                <form>
                                    <input type="text" value={search} id='search-product' onChange={(e) => setsearch(e.target.value)} className='form-control p-1 border-end-0 border-start-0 border-primary' placeholder='Pala Search' />
                                </form>
                            </div>
                            <i className="fa-solid fa-magnifying-glass ms-2 pt-2 fs-5 text-primary" data-bs-toggle="collapse" data-bs-target="#collapseExample" aria-expanded="false" aria-controls="collapseExample">
                            </i>
                        </div>
                        <div className='position-relative'>
                            <Link className="fa-solid fa-cart-shopping fs-5 pt-2 text-primary px-2 text-decoration-none" to="/cart"></Link>
                            {itemamount ? <span className="position-absolute top-0 start-100 mt-1 translate-middle badge rounded-pill bg-danger">{itemamount}</span> : ""}

                        </div>
                    </ul>
                </div>


            </div>
        </nav>
    )
}
