/* eslint-disable no-undef */
/* eslint-disable no-restricted-globals */
import axios from 'axios'
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
export default function Regstrition() {

    let [errMsg, setErrrMsg] = useState("")
    let [loding, setloding] = useState(false)
    let Navigate = useNavigate()
    let Baseurl = "https://route-ecommerce.onrender.com"
    let validate = Yup.object({
      name: Yup.string().required().min(2, "min char 2").max(15, "max length 15"),
      email: Yup.string().required().email("enter vaild Email"),
      phone: Yup.string().required().matches(/^(010|011|012|015)[0-9]{8}$/, "enter vaild number"),
      password: Yup.string().required().matches(/^[A-Z][a-zA-Z0-9!@#$%^&()_-]{3,16}$/, "must  first char is capital"),
      rePassword: Yup.string().required().oneOf([Yup.ref("password")], "repassword not match"),
    })
    let formik = useFormik({
      initialValues: {
        name: "",
        email: "",
        phone: "",
        password: "",
        rePassword: "",
      },
  
      validationSchema: validate,
  
      onSubmit: (x) => {
        setRegisterData(x)
  
      }
    })
  
    async function setRegisterData(objData) {
      setloding(true)
      let { data } = await axios.post(`${Baseurl}/api/v1/auth/signup`, objData).catch((error) => {
        setloding(false)
        setErrrMsg(error.response.data.errors.msg)
      })
  
      setloding(false)
      if (data.message == 'success') {
        Navigate('/login')
      }
    }

    return (

        <>
            <div className="bg-light text-center p-5">
                <h3 className='fs-1 test pt-5'>Register</h3>
                <p className='pb-5'>Home-Regstrition</p>
            </div>
            <div className='about-image '>
                <div className="container p-0 log mt-5 colorregs rounded-3">
                    <div className="row me-0 align-items-center">
                        <div className="col-md-6  col-sm-12">
                            <div className="text-center">
                                <h4 className='text-white'>Already have an account?</h4>
                                <p className='text-white py-3'>There are advances being made in science and technology everyday, and a good example of this is the</p>
                                <Link to="/login" className='btn btn-danger text-decoration-none'>Login</Link>
                            </div>
                        </div>
                        <div className="col-md-6 ">
            <h3 className="text-white my-5 text-center">Create My Account!</h3>
            <form onSubmit={formik.handleSubmit}>
              <div className='my-3'>

                <input onChange={formik.handleChange} type="text" name='name' id="name" className='form-control text-white' placeholder='Enter Your Name' />
                <p className='text-danger'>{formik.errors.name}</p>
              </div>
              <div className='my-3'>
                <input onChange={formik.handleChange} type="email" name='email' id="email" className='form-control text-white' placeholder='Enter Your Email' />
                <p className='text-danger'>{formik.errors.email}</p>
              </div>
              <div className='my-3'>
                <input onChange={formik.handleChange} type="password" name='password' id="password" className='form-control text-white' placeholder='Enter Your Password' />
                <p className='text-danger'>{formik.errors.password}</p>
              </div>
              <div className='my-3'>

                <input onChange={formik.handleChange} type="password" name='rePassword' id="rePassword" className='form-control text-white' placeholder='rePassword' />
                <p className='text-danger'>{formik.errors.rePassword}</p>
              </div>
              <div className='my-3'>

                <input onChange={formik.handleChange} type="text" name='phone' id="phone" className='form-control text-white' placeholder='Your Phone' />
                <p className='text-danger'>{formik.errors.phone}</p>
              </div>
              {errMsg != "" ? <div className='alert alert-danger'>{errMsg}</div> : ""}
              {loding ? <button type='button' className='btn btn-danger p-2 bt w-100'><i className="fa-solid fa-spinner fa-spin text-white"></i></button> : <button type='submit' className='btn btn-danger p-2 bt w-100'>Create Account</button>}
              <p className='text-muted prag py-1'>This site is protected by reCAPTCHA and the Google Privacy Policy and Terms of Service apply.</p>
              <hr />

            </form>
          </div>

                    </div>
                </div>
            </div>


        </>

    )
}
