import axios from 'axios'
import React, { useEffect, useState, useContext } from 'react'
import { Link } from 'react-router-dom'
import Middlesection from './Middlesection'
import $ from 'jquery';
import { SearchContext } from './FilterPorducts-context'

export default function Product() {
    let [product, setproduct] = useState([])
    const [currentProducts, setCurentProducts] = useState([])
    const { searchValue, filtedProducts } = useContext(SearchContext)

    useEffect(() => {
        GetAllproduct()
    }, [])

    useEffect(() => {
        if (filtedProducts.length !== 0 && searchValue) {
            setCurentProducts(filtedProducts)
        }

        if (searchValue === "") {
            setCurentProducts(product)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [searchValue])

    useEffect(() => {
        if (product.length !== 0) {
            setCurentProducts(product)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [product])

    async function GetAllproduct() {
        let { data } = await axios.get("https://fakestoreapi.com/products")
        setproduct(data)
        $(".loading").fadeOut(2000)
    }

    return (<>
        <div className='loading'>
            <div className="sk-circle">
                <div className="sk-circle1 sk-child"></div>
                <div className="sk-circle2 sk-child"></div>
                <div className="sk-circle3 sk-child"></div>
                <div className="sk-circle4 sk-child"></div>
                <div className="sk-circle5 sk-child"></div>
                <div className="sk-circle6 sk-child"></div>
                <div className="sk-circle7 sk-child"></div>
                <div className="sk-circle8 sk-child"></div>
                <div className="sk-circle9 sk-child"></div>
                <div className="sk-circle10 sk-child"></div>
                <div className="sk-circle11 sk-child"></div>
                <div className="sk-circle12 sk-child"></div>
            </div>
        </div>
        <div className='container my-5'>
            <div className='row my-5'>
                <div className="col">
                    <div className='pos'>
                        <p className='text-muted'>Popular Item in the market</p>
                        <h3 className='px-4 fs-3 trind pb-3'>Trending Product</h3>
                    </div>
                </div>
            </div>
            <div className="row">
                {currentProducts.map((el, i) => (
                    <div className="col-md-3" key={i}>
                        <div className="cardgruop ">
                            <div className="card position-relative shadow ">
                                <img src={el.image} className="image" alt="" />
                                <div className='laye d-flex justify-content-center align-items-center'>
                                    <Link className='text-decoration-none text-white' to={"/productdeteils/" + el.id}>
                                        <div className='layr rounded-1  p-2 me-2 '>
                                            <i className="fa-solid fa-magnifying-glass fs-5"></i>
                                        </div>
                                    </Link>
                                    <div className='layr rounded-1  p-2'>
                                        <Link className="fa-solid fa-cart-shopping fs-5 text-decoration-none text-white" to="/cart"></Link>
                                    </div>
                                    <div className='layr rounded-1  p-2 ms-2'>
                                        <i className="fa-regular fa-heart fs-5"></i>
                                    </div>
                                </div>
                            </div>
                            <div className='cloection text-center py-2'>
                                <Link className='text-decoration-none text-dark' to={"/productdeteils/" + el.id}>
                                    <h5 className='tex'>{el.title.split(" ").slice(0, 3).join(" ")}</h5>
                                </Link>
                                <p className='font'><span className='text-primary fs-5'>price</span>: {el.price}</p>
                            </div>
                        </div>
                    </div>
                ))}
            </div>
        </div>
        <Middlesection />
    </>
    )
}
