import axios from "axios";
import { createContext, useEffect, useState } from "react";

const inialProducts = [];

export let SearchContext = createContext({
  searchValue: "",
  filtedProducts: [],
  getSearchValueFun: (search) => {},
});

export default function SearchContextProvider(props) {
  const [searchValue, setSearchValue] = useState("");
  const [products, setProducts] = useState(inialProducts);
  const [filtedProducts, setFilteredproducts] = useState([]);

  useEffect(() => {
    async function GetAllproduct() {
      let { data } = await axios.get("https://fakestoreapi.com/products");
      setProducts(data);
    }

    GetAllproduct();
  }, []);

  const getSearchValueFun = (search) => {
    setSearchValue(search);
    //filter products
    if (products.length !== 0) {
      const filterData = products.filter((product) =>
        product.title.toLowerCase().includes(search.toLowerCase())
      );
      setFilteredproducts(filterData);
    }
  };

  return (
    <SearchContext.Provider
      value={{ searchValue, filtedProducts, getSearchValueFun }}
    >
      {props.children}
    </SearchContext.Provider>
  );
}
