import axios from 'axios'
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { useContext } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
import { Cartcontext } from './Cartcontext'
export default function Login() {
    let [errMsg, setErrrMsg] = useState("")
    let [loding, setloding] = useState(false)
    let { clear } = useContext(Cartcontext)
    let Navigate = useNavigate()
    let Baseurl = "https://route-ecommerce.onrender.com"
    let validate = Yup.object({
        email: Yup.string().required().email("enter vaild Email"),
        password: Yup.string().required().matches(/^[a-zA-Z0-9!@#$%^&()_-]{3,16}$/, "enter vaild password"),
    })
    let formik = useFormik({
        initialValues: {
            email: "",
            password: "",
        },

        validationSchema: validate,

        onSubmit: (x) => {
            setRegisterData(x)
       
        }
    })

    async function setRegisterData(objData) {
        setloding(true)
        let { data } = await axios.post(`${Baseurl}/api/v1/auth/signin`, objData).catch((error) => {
            setloding(false)
            setErrrMsg(error.response.data.message)
        })
        setloding(false)
        if (data.message == 'success') {
            Navigate('/product')
            clear()
            alert("Well Done! Your Order Is Successfully Send")
        }
    }





    return (
        <>
            <div className="bg-light text-center p-5">
                <h3 className='fs-1 test pt-5'>Login</h3>
                <p className='pb-5'>Home-Login</p>
            </div>
            <div className='about-image '>
                <div className="container p-0 log mt-5 colorregs rounded-3">
                    <div className="row me-0 align-items-center">
                        <div className="col-md-6  col-sm-12">
                            <div className="text-center">
                                <h4 className='text-white'>New to our website?</h4>
                                <p className='text-white py-3'>There are advances being made in science and technology everyday, and a good example of this is the</p>
                                <Link to="/regstrition" className='btn btn-danger'>Create Acount</Link>
                            </div>
                        </div>
                        <div className="col-md-6 ">
                            <h3 className="text-white my-5 text-center">Login </h3>
                            <form onSubmit={formik.handleSubmit}>
                                <div className='mt-5'>
                                    <input onChange={formik.handleChange} type="email" name='email' id="email" className='form-control' placeholder='Email Address' />
                                    <p className='text-danger'>{formik.errors.email}</p>
                                </div>
                                <div className='my-4 '>
                                    <input onChange={formik.handleChange} type="password" name='password' id="password" className='form-control' placeholder='Password' />
                                    <p className='text-danger'>{formik.errors.password}</p>
                                </div>
                                {errMsg != "" ? <div className='alert alert-danger'>{errMsg}</div> : ""}
                                <br />
                                {loding ?
                                    <button type='button' className='btn btn-danger p-2 bt w-100 text-white mb-3' ><i className="fa-solid fa-spinner fa-spin text-white"></i></button>
                                    :
                                    <button type='submit' className='btn btn-danger p-2 bt w-100 text-white mb-3'>Login</button>}
                            </form>

                        </div>

                    </div>
                </div>
            </div>


        </>
    )
}
