import React, { useContext } from 'react'
import { Cartcontext } from './Cartcontext'
import { Link } from 'react-router-dom';

export default function Cart() {
   
    const { cart, removecard, clear, increseitem, decreseitem, total, itemamount } = useContext(Cartcontext)
    return (
        <>

            <div className="bg-light text-center p-5">
                <h3 className='fs-1 test pt-5'>Shoping Cart</h3>
                <p className='pb-5'>Home.Shoping Cart</p>
            </div>

            <div className="container">
                <div className="row text-center">
                    <div className="col-sm-12">
                        <table className="table" style={{ verticalAlign: "middle" }} >
                            <thead>
                                <tr>
                                    <th className="col-sm-3 tex">Product</th>
                                    <th className="col-sm-3 tex">Quantity</th>
                                    <th className="col-sm-3 tex">Total</th>
                                    <th className="col-sm-3 tex">Del</th>

                                </tr>
                            </thead>
                            <tbody>

                                {cart.map((el) => {
                                    return <tr key={el.id}>
                                        <td >
                                            <img src={el.image} className="w-25" alt="" />
                                            <p className='pt-3 tex'>{el.price} Gp</p>
                                        </td>

                                        <td>
                                            <button className="increase items-count btn btn-success p-2 pt-0 pb-0 rounded-4 me-1" type="button" onClick={() => { increseitem(el.id, el.amount) }}>+</button>
                                            <input type="text" className='w-25 text-center form-control-sm  border-start-0 border-end-0  tex' placeholder={el.amount} />
                                            <button className="reduced items-count btn btn-warning  p-2 pt-0 pb-0 rounded-4 ms-1" type="button" onClick={() => { decreseitem(el.id, el.amount) }} >-</button>
                                        </td>
                                        <td >
                                            {el.price ? <p className='tex'>{(el.price * el.amount).toFixed(2)} Gp </p> : ""}
                                        </td>
                                        <td>
                                            <i className='text-danger fa-solid fa-trash fs-5' onClick={() => { removecard(el.id) }}></i>
                                        </td>
                                    </tr>
                                })}
                            </tbody>
                        </table>
                    </div>
                    {total ? <div className='alert alert-success d-flex justify-content-between'>
                        <p className='text-muted fw-bold'> You Pay is :</p>
                        <p className='text-muted fw-bold'> {total} GP</p>
                        <Link className='btn btn-outline-primary rounded-3 add' to="/login" >Apply</Link>

                    </div>
                        : ''}

                    <div className="d-flex justify-content-end mt-3 ">
                        {itemamount ? <button className='btn btn-outline-danger rounded-3 me-3' onClick={() => { clear() }}>Clear</button> : <button className='btn btn-outline-danger rounded-3 me-3' onClick={() => { clear() }}>Clear</button>}
                    </div>
                </div>
            </div>


        </>
    )
}
